import http from '../../http-common'

class RestrictionsTypeervice {
  getAll () {
    return http.get('RestrictionsType')
  }

  getAllPagnation (params) {
    return http.get('RestrictionsType', { params })
  }

  get (id) {
    return http.get(`RestrictionsType/getById?Id=${id}`)
  }

  create (data) {
    return http.post('RestrictionsType', data)
  }

  update (id, data) {
    return http.put(`RestrictionsType?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`RestrictionsType?Id=${id}`)
  }
}

export default new RestrictionsTypeervice()
